.note {
  padding: 20px;
  background-color: #fff;
  border: 2px solid #ccc;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.note .add-note {
  padding: 10px 14px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  background-color: #07d1ec;
  color: #fff;
  margin-bottom: 10px;
}
